
.slide-in-left-exit-active{
    transform: translateX(-100%);
    opacity: 0;
    transition: all 200ms;
}

.center{
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
}

.users-list{
    list-style: none;
    margin: 0 auto;
    padding: 0;
    width: 90%;
    max-width: 50rem;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}